"use client";
import { useWallet, useConnection } from "@solana/wallet-adapter-react";
import { PublicKey, Transaction, SystemProgram } from "@solana/web3.js";
import { getAssociatedTokenAddress, createTransferInstruction } from "@solana/spl-token";
import { useState, useEffect } from "react";
import { WalletMultiButton } from "@solana/wallet-adapter-react-ui";

const TOKEN_MINT = new PublicKey("EtKn4MuGbztsSYn1Q94DNtxXRLGGg7wdAU67JGxVkH4p"); // $FOWLCAT mint
const STAKING_PROGRAM_ID = new PublicKey("YOUR_STAKING_PROGRAM_ID_HERE"); // update

export default function StakingPage() {
  const { connection } = useConnection();
  const { publicKey, sendTransaction } = useWallet();
  const [balance, setBalance] = useState<number>(0);
  const [amount, setAmount] = useState("");

  useEffect(() => {
    const fetchBalance = async () => {
      if (!publicKey) return;
      try {
        const tokenAccount = await getAssociatedTokenAddress(TOKEN_MINT, publicKey);
        const info = await connection.getTokenAccountBalance(tokenAccount);
        setBalance(parseFloat(info.value.amount) / 1e9); // adjust decimals if needed
      } catch {
        setBalance(0);
      }
    };
    fetchBalance();
  }, [publicKey, connection]);

  const handleStake = async () => {
    if (!publicKey || !amount) return;

    // Example placeholder stake transaction (youâ€™ll replace this with your staking program instruction)
    const lamports = parseFloat(amount) * 1e9;
    const tx = new Transaction().add(
      SystemProgram.transfer({
        fromPubkey: publicKey,
        toPubkey: STAKING_PROGRAM_ID,
        lamports,
      })
    );
    await sendTransaction(tx, connection);
  };

  return (
    <main className="max-w-xl mx-auto p-8 space-y-8">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-semibold">$FOWLCAT Staking</h1>
        <WalletMultiButton />
      </div>

      {!publicKey ? (
        <p>Connect your wallet to begin.</p>
      ) : (
        <div className="space-y-6">
          <div className="rounded-2xl border p-4">
            <p className="text-sm opacity-70">Wallet Balance</p>
            <p className="text-lg font-medium">{balance.toLocaleString()} FOWLCAT</p>
          </div>

          <div className="flex gap-3 items-end">
            <input
              className="flex-1 rounded-xl border px-3 py-2 bg-transparent"
              placeholder="0.0"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
            />
            <button
              onClick={handleStake}
              className="rounded-2xl px-4 py-2 border hover:bg-white/10 transition"
            >
              Stake
            </button>
          </div>
        </div>
      )}
    </main>
  );
}
